import React from "react";
import { Row, Col } from "react-bootstrap";
import { Link } from "gatsby";
import TalkToUsNew from "@components/common/talk-to-us-new";

const SmartSolution = (props) => {
  const { db } = props;
  return (
    <div className="smart_dev_solutions_wrapper">
      <Row>
        <Col md={8} className="mx-auto">
          <h2 className="text-center">
            Smart Contract Development Solutions in various Industries
          </h2>
        </Col>
      </Row>
      <Row className="smart_sol_card_layout">
        <Col lg={4} md={6} col={12} className="smart_sol_card">
          <div className="smart_sol_header">
            <div className="smart_sol_img">
              {/* {utils.smart_dex()} */}
              </div>
            <div className="smart_sol_title">
              Smart Contracts
              <br /> For DEXs
            </div>
          </div>
          <div className="smart_sol_body">
            Decentralized crypto exchange smart contract experts ensure
            automation, secure data handling, and optimal performance for
            confidentiality in contract management.
          </div>
        </Col>
        <Col lg={4} md={6} col={12} className="smart_sol_card">
          <div className="smart_sol_header">
            <div className="smart_sol_img">
              {/* {utils.smart_wallet()} */}
              </div>
            <div className="smart_sol_title">Smart Contracts for Wallets</div>
          </div>
          <div className="smart_sol_body">
            Elevate crypto wallet security with our smart contract development
            services, enabling the launch of a highly secure and reliable wallet
            for your company's cryptocurrency assets.
          </div>
        </Col>
        <Col lg={4} md={6} col={12} className="smart_sol_card">
          <div className="smart_sol_header">
            <div className="smart_sol_img">
              {/* {utils.smart_nft()} */}
              </div>
            <div className="smart_sol_title">
              Smart Contracts
              <br /> for NFTs
            </div>
          </div>
          <div className="smart_sol_body">
            Leverage our expertise for secure customized NFT smart contracts.
            Our professionals ensure top notch security, tailor solutions to
            meet your specific non-fungible token requirements.
          </div>
        </Col>
        <Col lg={4} md={6} col={12} className="smart_sol_card">
          <div className="smart_sol_header">
            <div className="smart_sol_img">
              {/* {utils.smart_farming()} */}
              </div>
            <div className="smart_sol_title">
              Smart Contracts <br /> For Yield Farming
            </div>
          </div>
          <div className="smart_sol_body">
            Crafting smart contracts with meticulously coded buyer-seller
            agreements, we facilitate yield farming across decentralized finance
            (DeFi) for enhanced financial opportunities.
          </div>
        </Col>
        <Col lg={4} md={6} col={12} className="smart_sol_card">
          <div className="smart_sol_header">
            <div className="smart_sol_img">
              {/* {utils.smart_token()} */}
              </div>
            <div className="smart_sol_title">
              Token Smart <br />
              Contracts
            </div>
          </div>
          <div className="smart_sol_body">
            Leveraging Ethereum, we create smart contracts automating ERC-20
            token transfers between wallets, streamlining processes and
            enhancing efficiency to save valuable time.
          </div>
        </Col>
        <Col lg={4} md={6} col={12} className="smart_sol_card">
          <div className="smart_sol_header">
            <div className="smart_sol_img">
              {/* {utils.smart_app()} */}
              </div>
            <div className="smart_sol_title">
              Smart Contract <br />
              For dApps
            </div>
          </div>
          <div className="smart_sol_body">
            Our emphasis lies in ensuring the functionality, trustworthiness of
            dApps and smart contracts. We prioritize high scalability to
            accommodate new applications seamlessly.
          </div>
        </Col>
        <Col md={12}>
          <div className="crypto_btns_grp">
            <Link className="prime_submit_btn" to="/contact-us/">
              View Live Demo
            </Link>
            <button className="second_consult_btn" type="button">
              <TalkToUsNew label="Free Consult" db={db} />
            </button>
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default SmartSolution;
