import React from "react";
import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { utils } from "@core/helper";
import mongodb from "@styles/assets/images/FramworksHD/mongo.png";
import zeppline from "@styles/assets/images/FramworksHD/openzeppline.png";
import quicknode from "@styles/assets/images/FramworksHD/quicknode.png";
import rust from "@styles/assets/images/FramworksHD/rust.png";
import trustwallet from "@styles/assets/images/FramworksHD/trustwallet.png";
import infura from "@styles/assets/images/FramworksHD/infura.png";
import coinbase from "@styles/assets/images/FramworksHD/coinbase.png";
import binance from "../../styles/assets/images/utilsicons/binance.svg";
import viper from "../../styles/assets/images/utilsicons/viper.svg";
import chainLink from "../../styles/assets/images/utilsicons/chainLink.svg";
import hardhat from "../../styles/assets/images/utilsicons/hardhat.svg";
import gonache from "../../styles/assets/images/utilsicons/gonache.svg";
